import React from "react";
import Card from "react-bootstrap/Card";
import { ImPointRight } from "react-icons/im";

function AboutCard() {
  return (
    <Card className="quote-card-view">
      <Card.Body>
        <blockquote className="blockquote mb-0">
          <p style={{ textAlign: "justify" }}>
            Hi Everyone, I am <span className="purple">Khashayar Haghighi </span>
            from <span className="purple"> Esfahan, Iran.</span>
            <br />I am a senior pursuing B.Sc in Computer Engineering in IAUN.
            <br />
            <br />
            Apart from coding, some other topics that I am intrested in!
          </p>
          <ul>
            <li className="about-activity">
              <ImPointRight /> MicroControllers
            </li>
            <li className="about-activity">
              <ImPointRight /> Internet Of Things
            </li>
            <li className="about-activity">
              <ImPointRight /> Computer Networks
            </li>
          </ul>

          <p style={{ marginBlockEnd: 0, color: "rgb(155 126 172)" }}>
            "Strive to build things that make a difference!"{" "}
          </p>
          <footer className="blockquote-footer">Khashayar</footer>
        </blockquote>
      </Card.Body>
    </Card>
  );
}

export default AboutCard;
